/*Landing Page*/
.header-color {
  background: #355C7D;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #C06C84, #6C5B7B, #355C7D);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #C06C84, #6C5B7B, #355C7D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  }
  


 .landing-grid {
background: #355C7D;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #C06C84, #6C5B7B, #355C7D);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #C06C84, #6C5B7B, #355C7D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align:center;
position:fixed;
top:2;
left:0;
width:100%;
height:100%;
} 

.avatar-img {
  height: 250px;
  border-radius: 40%;

}

.banner-text {

  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius:10px;

}

.banner-text h1 {
  display:flex;
  font-size: 66px;
  font-weight: bold;
  color:white;
  flex-direction: row;
  justify-content: center;
}
.banner-text h2 {
  display:flex;
  font-size: 33px;
  font-weight: lighter;
  color:white;
  flex-direction: row;
  justify-content: center;
}
.banner-text h3 {
  display:flex;
  font-size: 22px;
  font-weight: lighter;
  color:white;
  flex-direction: row;
  justify-content: center;
}

.banner-text hr {
  display:flex;
  border-top:5px dotted white;
  width: 50%;
  margin:auto;
  flex-direction: row;
  justify-content: center;

}

.banner-text p {
  display:flex;
  color: white;
  font-size:20px;
  padding: 1em;
  flex-direction:row;
  justify-content:center;

}

.social-links {
  display: flex;
  justify-content: space-between;
  width:50%;
  margin:auto;

}

.social-links i {
  color:white;
  font-size: 5em;
  padding: 8px;
}



/*Projects Page*/




.category-tabs {
  
    text-align:center;
    position:fixed;
    margin:auto;
    left:0;
    width:100%;
    height:100%;
    background: #355C7D;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #C06C84, #6C5B7B, #355C7D);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #C06C84, #6C5B7B, #355C7D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        
        }
        


    



 /*Contct Page*/

 .contact-body {
   margin:auto;
  
   position:fixed;
   width: 100%;
   height: 100%;
   justify-content:center;
   padding-top:2em;
 } 

 .contact-grid {
   text-align:center;
   justify-content:center;
   width:80%;
   height:550px;
   background: white;
   color:black;
   
   margin-bottom: 20px;
 } 

 
.contact-grid h2 {
  display:center;
  justify-content: center;
  font-family: 'Playfair Display', serif;
}

.contact-grid p {
  font-family: 'Playfair Display', serif;
  font-weight: bold;
}

.contact-grid hr {
 
  border-top: 5px dotted ;
  width:50%;
  margin:auto;

}


.contact-list i {

  font-size: 45px;
  padding-right: 2rem;

}


/*Resume*/

.resume-right-col {
  background: #27221f;
  color:white;
  padding:2em;
}

.footNote h5 {
  font-size: 9px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
